// LOGOTYPE :: START

.logonav {
  &__link {
    display: flex;
    align-items: flex-start;
  }

  &__img {
    display: block;
    max-width: 202px;
    height: auto;
    // width: 202px;
    @include respond(max-small) {
      height: calc(43px / 2);
    }
  }
}

.logofooter {
  &__link {
    display: flex;
    align-items: flex-start;
  }

  &__img {
    display: block;
    max-width: 136px;
    height: auto;
    // width: 136px;
    @include respond(max-small) {
      height: calc(43px / 2);
    }
  }
}

// LOGOTYPE :: END