// SECTION badges :: START

.badges {
  padding-top: 100px;
  padding-bottom: 89px;

  &__wrapper {
    display: flex;
    align-items: center;
    justify-content: center;

    &-1 {
      display: none;
      padding-top: 22px;
      @include respond(max-small) {
        display: flex;
      }
    }
  }

  &__box {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-left: 26px;
    margin-right: 26px;
    filter: grayscale(80%);
    @include respond(max-large) {
      margin-left: 18px;
      margin-right: 18px;
    }
    @include respond(max-small) {
      margin-left: 10px;
      margin-right: 10px;
    }

    &-5,
    &-6 {
      @include respond(max-small) {
        display: none;
      }
    }

    img {
      display: block;
      max-width: 70%;
      height: auto;
    }
  }
}

// SECTION badges :: END