// SECTION :: START

.ourOffers {

  // base
  .base {
    &__box {
      &-head {
        > div {
          &:nth-of-type(2) {
            a {
              flex-direction: column;
              height: 65px;
              line-height: 1;

              span {
                font-size: 16px;
              }
            }
          }
        }
      }

      &-body {
        > div {
          width: 100%;

          > div {
            width: 100%;
          }
        }

        .base__price {
          flex-direction: column-reverse;

          > div {
            padding-top: 0;

            a {
              white-space: nowrap;
              padding: 0 30px;
            }
          }
        }
      }
    }

    &__collapse {
      position: relative;
      padding-top: 50px;
      margin-bottom: 50px;

      &:after {
        content: '';
        position: absolute;
        top: 100%;
        left: 0;
        right: 0;
        width: calc(100% - 280px);
        height: 1px;
        background-color: #cdd7df;
        margin-left: auto;
        margin-right: auto;
        @include adaptive(1439) {
          width: 100%;
        }
      }
    }
  }

  &__textLine {
    display: flex;
    align-items: center;
    width: 100%;
    @include adaptive(767) {
      padding-left: 15px;
      padding-right: 15px;
    }

    a {
      white-space: nowrap;
      margin: 0;
      padding: 0 !important;
      padding-right: 15px !important;
      background-color: transparent !important;
      color: #4d5964 !important;
      font-size: 14px;
      text-decoration: underline;

      &:hover {
        color: #4d5964 !important;
      }
    }

    span {
      flex: 1;
      min-width: 1px;
      max-width: 100%;
      height: 1px;
      background-color: #cdd7df;
    }
  }

  &__desc {
    position: relative;
    padding-bottom: 22px;

    &:after {
      content: '';
      position: absolute;
      bottom: 0;
      left: 0;
      right: 0;
      width: calc(100% - 280px);
      height: 1px;
      background-color: #cdd7df;
      margin-left: auto;
      margin-right: auto;
      @include adaptive(1439) {
        width: 100%;
      }
    }

    > div {
      width: 100%;
      max-width: 1070px;
      margin-left: auto;
      margin-right: auto;
      padding-bottom: 22px;
      @include adaptive(767) {
        padding-left: 15px;
        padding-right: 15px;
      }
    }

    &-title {
      padding-bottom: 40px;
      line-height: 1.2;
      color: #0a1723;
      font-size: 36px;
      @include adaptive(767) {
        padding-bottom: 30px;
        font-size: 30px;
      }
    }

    &-text {
      padding-bottom: 30px;
      color: #4f6a7a;
      line-height: 1.8;
      font-size: 16px;
    }

    &-btn {
      display: flex;
      align-items: center;
      justify-content: center;
      position: relative;
      width: 100%;
      max-width: 429px;
      margin-left: auto;
      margin-right: auto;
      height: 48px;
      padding-left: 15px;
      padding-right: 15px;
      border-radius: 24px;
      background-color: #ecf7ff;
      @include transition(background-color);

      &:hover {
        background-color: #c63e47;
        @include transition(background-color);

        img {
          &:nth-of-type(1) {
            opacity: 0;
            @include transition(opacity);
          }
          &:nth-of-type(2) {
            opacity: 1;
            @include transition(opacity);
          }
        }

        span {
          color: #ffffff;
          @include transition(color);
        }
      }

      &:active {
        background-color: #0C1722;
        @include transition(background-color);

        img {
          &:nth-of-type(1) {
            opacity: 1;
            @include transition(opacity);
          }
          &:nth-of-type(2) {
            opacity: 0;
            @include transition(opacity);
          }
        }

        span {
          color: #ffffff;
          @include transition(color);
        }
      }

      img {
        position: absolute;
        top: -17px;
        left: 36px;
        @include transition(opacity);
        @include adaptive(575) {
          left: 16px;
        }

        &:nth-of-type(2) {
          opacity: 0;
        }
      }

      span {
        padding-left: 100px;
        color: #0a1723;
        font-size: 16px;
        font-weight: 500;
        @include transition(color);
      }
    }
  }

  &__main {
    position: relative;
    width: 100%;
    max-width: 882px;
    margin-left: auto;
    margin-right: auto;
    padding-top: 141px;
    padding-bottom: 141px;
    margin-bottom: 46px;
    @include adaptive(991) {
      padding-top: 100px;
      padding-bottom: 100px;
    }
    @include adaptive(767) {
      padding: 100px 15px;
    }
    @include adaptive(575) {
      padding: 25px 15px 75px;
    }
    @include adaptive(374) {
      padding: 0 15px 75px;
    }

    &:after {
      content: '';
      position: absolute;
      bottom: 0;
      left: 0;
      right: 0;
      width: calc(100% - 280px);
      height: 1px;
      background-color: #cdd7df;
      margin-left: auto;
      margin-right: auto;
      @include adaptive(1439) {
        width: 100%;
      }
    }

    &-container {
      display: flex;
      flex-direction: column;
      justify-content: center;
      position: relative;
      height: 251px;
      padding-top: 57px;
      padding-bottom: 57px;
      padding-left: 280px;
      background-image: url('../img/img-ourOffers-mian@2x.png');
      background-repeat: no-repeat;
      background-position: center;
      background-size: contain;
      @include adaptive(991) {
        padding-left: 230px;
        padding-bottom: 77px;
      }
      @include adaptive(767) {
        padding-left: 170px;
      }
      @include adaptive(575) {
        padding-left: 120px;
        padding-bottom: 57px;
      }
      @include adaptive(424) {
        padding-left: 105px;
      }
      @include adaptive(374) {
        padding-left: 90px;
        padding-right: 5px;
      }
    }

    p {
      display: flex;
      font-size: 28px;
      font-weight: bold;
      @include adaptive(991) {
        font-size: 24px;
      }
      @include adaptive(767) {
        font-size: 18px;
      }
      @include adaptive(575) {
        font-size: 14px;
      }
      @include adaptive(424) {
        font-size: 13px;
      }
      @include adaptive(374) {
        font-size: 12px;
      }
    }

    span {
      display: flex;
      font-size: 24px;
      @include adaptive(991) {
        font-size: 21px;
      }
      @include adaptive(767) {
        font-size: 16px;
      }
      @include adaptive(575) {
        font-size: 12px;
      }
      @include adaptive(424) {
        font-size: 11px;
      }
      @include adaptive(374) {
        font-size: 10px;
      }
    }

    a {
      position: absolute;
      bottom: 32px;
      right: 33px;
      display: flex;
      align-items: center;
      justify-content: center;
      width: 100%;
      max-width: 250px;
      height: 50px;
      border-radius: 25px;
      border: 2px solid #d72d42;
      background-color: #d72d42;
      color: #ffffff;
      font-size: 18px;
      font-weight: 500;
      @include transition(background-color, color);
      @include adaptive(991) {
        bottom: 42px;
      }
      @include adaptive(767) {
        right: 15px;
        bottom: 66px;
        max-width: 200px;
        height: 35px;
        font-size: 16px;
      }
      @include adaptive(575) {
        bottom: 0;
        left: 0;
        right: 0;
        margin: 0 auto;
      }

      &:hover {
        color: #d72d42;
        background-color: #fff;
        @include transition(background-color, color);
      }
    }
  }

  &__btn {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    max-width: 360px;
    height: 48px;
    margin-left: auto;
    margin-right: auto;
    padding: 5px 15px;
    border-radius: 25px;
    background-color: #ecf7ff;
    font-size: 16px;
    font-weight: 500;

    &-1 {
      margin-bottom: 75px;
    }

    .product__collapse-wrapper.product__collapse-wrapper--list & {
      @include adaptive(767) {
        display: none;
      }
    }

    img {
      position: absolute;
      top: -17px;
      left: 36px;
      @include transition(opacity);
      @include adaptive(575) {
        left: 16px;
      }

      &:nth-of-type(2) {
        opacity: 0;
      }
    }

    &:hover {
      background-color: #c63e47;
      @include transition(background-color);

      img {
        &:nth-of-type(1) {
          opacity: 0;
          @include transition(opacity);
        }
        &:nth-of-type(2) {
          opacity: 1;
          @include transition(opacity);
        }
      }

      span {
        color: #ffffff;
        @include transition(color);
      }
    }

    &:active {
      background-color: #0C1722;
      @include transition(background-color);

      img {
        &:nth-of-type(1) {
          opacity: 1;
          @include transition(opacity);
        }
        &:nth-of-type(2) {
          opacity: 0;
          @include transition(opacity);
        }
      }

      span {
        color: #ffffff;
        @include transition(color);
      }
    }

    span {
      padding-left: 80px;
      color: #0a1723;
    }
  }

  &__info {
    &-wrapper {
      width: 100%;
      max-width: 1230px;
      margin-left: auto;
      margin-right: auto;
      padding: 65px 15px 75px;
      margin-bottom: 50px;
      border-top: 1px solid #cdd7df;
      border-bottom: 1px solid #cdd7df;
      @include adaptive(767) {
        padding-bottom: 30px;
      }
    }

    &-title {
      display: flex;
      flex-direction: column;
      line-height: 1.6;
      font-size: 36px;
      font-weight: 500;
      @include adaptive(1439) {
        font-size: 33px;
      }
      @include adaptive(1279) {
        line-height: 1.4;
        font-size: 30px;
      }
      @include adaptive(767) {
        font-size: 24px;
        text-align: center;

        .s-brand & {
          font-size: 16px;
        }
      }

      span {
        font-weight: bold;
      }
    }

    &-box {
      cursor: pointer;
      display: flex;
      align-items: flex-start;
      justify-content: space-between;
      padding-top: 50px;
      @include adaptive(1279) {
        flex-wrap: wrap;
      }

      > div {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        @include adaptive(1279) {
          width: 100%;
          max-width: calc(100% / 3);
          padding-bottom: 30px;
        }
        @include adaptive(767) {
          width: 100%;
          max-width: calc(100% / 2);
        }

        &:hover {
          span {
            img {
              &:nth-of-type(2) {
                opacity: 1;
                @include transition(opacity);
              }
            }
          }
        }

        &:active {
          span {
            img {
              &:nth-of-type(2) {
                opacity: 0;
                @include transition(opacity);
              }
            }
          }

          p {
            color: #d72d42;
            @include transition(color);
          }
        }
      }

      span {
        position: relative;
        width: 100%;
        height: 65px;

        img {
          display: block;
          max-width: 100%;
          height: auto;
          margin-left: auto;
          margin-right: auto;

          &:nth-of-type(2) {
            opacity: 0;
            position: absolute;
            top: 0;
            left: 0;
            right: 0;
            margin: 0 auto;
            @include transition(opacity);
          }
        }
      }

      p {
        width: 100%;
        padding-top: 10px;
        text-align: center;
        font-size: 24px;
        font-weight: bold;
        @include transition(color);
        @include adaptive(767) {
          font-size: 18px;

          .s-brand & {
            font-size: 16px;
          }
        }
      }
    }
  }
}

// SECTION :: END