.swiper {
  &-container {
    width: 100%;

    &--working {
      max-width: 500px;
      padding-bottom: 50px;

      .swiper {
        &-slide {
          img {
            display: block;
            max-width: 100%;
            height: auto;
            margin-left: auto;
            margin-right: auto;
          }
        }

        &-pagination {
          &-bullet {
            opacity: 1;
            border: 1px solid #e4eaef;
            background-color: transparent;
            @include transition(background-color);

            &-active {
              background-color: #e4eaef;
              @include transition(background-color);
            }
          }
        }

        &-button {
          &-prev,
          &-next {
            margin-top: 5%;
          }
        }
      }
    }

    &--save {
      max-width: 1300px;
      padding-bottom: 50px;

      .swiper {
        &-button {
          &-prev,
          &-next {
            top: 20%;
          }
        }
        &-slide {
          display: flex;
          align-items: center;
          height: 100%;
          @include respond(max-medium) {
            flex-wrap: wrap;
            flex-direction: column-reverse;
          }

          &-left,
          &-right {
            position: relative;
            width: 100%;
            height: 100%;
          }

          &-left {
            display: flex;
            flex-direction: column;
            width: 697px;
            padding-left: 130px;
            @include respond(max-medium) {
              padding-right: 65px;
              padding-left: 65px;
            }
            @include respond(max-small) {
              width: 100%;
              padding-left: 35px;
              padding-right: 35px;
            }
          }

          &-right {
            position: relative;
            max-width: calc(100% - 697px);
            min-height: 248px;
            padding-right: 90px;
            @include respond(max-medium) {
              max-width: 100%;
              min-height: 0;
              padding-right: 0;
            }
          }

          &-car {
            z-index: -1;
            position: absolute;
            display: flex;
            align-items: center;
            top: 0;
            right: 0;
            width: calc(100% + 107px);
            height: 100%;
            @include respond(max-medium) {
              position: relative;
              width: 100%;
            }
            @include respond(max-small) {
              padding-left: 50px;
              padding-right: 50px;
            }
            @include respond(max-extra-small) {
              padding-left: 35px;
              padding-right: 35px;
            }

            img {
              position: relative;
              top: 50px;
              display: block;
              max-width: 100%;
              height: auto;
              @include respond(max-medium) {
                top: 0;
                margin-left: auto;
                margin-right: auto;
              }
            }
          }
        }
      }
    }

    &--wiring-0,
    &--wiring-1 {
      padding-bottom: 50px;

      .swiper {
        &-slide {
          padding-left: 50px;
          padding-right: 50px;

          img {
            display: block;
            max-width: 100%;
            height: auto;
            margin-left: auto;
            margin-right: auto;
          }
        }

        &-pagination {
          &-bullet {
            opacity: 1;
            background-color: #95a6b2;

            &-active {
              background-color: #0a1723;
            }
          }
        }
      }
    }

    &--control {
      max-width: 1290px;
      padding-bottom: 50px;

      .swiper {
        &-slide {
          padding-left: 50px;
          padding-right: 50px;

          &-top,
          &-bottom {
            width: 100%;
            max-width: 1052px;
            margin-left: auto;
            margin-right: auto;
          }

          &-top {
            img {
              display: block;
              max-width: 100%;
              height: auto;
              margin-left: auto;
              margin-right: auto;
            }
          }

          &-bottom {
            display: flex;
            align-items: center;
            padding-top: 30px;
            @include respond(max-large) {
              flex-direction: column;
            }

            > div {
              &:nth-of-type(1) {
                width: 30%;
                @include respond(max-large) {
                  width: 100%;
                  text-align: center;
                }
              }
              &:nth-of-type(2) {
                display: flex;
                align-items: center;
                justify-content: flex-end;
                width: 70%;
                @include respond(max-large) {
                  flex-direction: column;
                  justify-content: center;
                  width: 100%;
                }
              }
            }
          }
        }

        &-pagination {
          &-bullet {
            opacity: 1;
            background-color: #95a6b2;

            &-active {
              background-color: #0a1723;
            }
          }
        }
      }
    }

    &--partners {
      padding-left: 30px;
      padding-right: 30px;

      .swiper {
        &-wrapper {
          display: flex;
          align-items: center;
        }
        &-slide {
          display: flex;
          align-items: center;
          justify-content: space-between;
          @include respond(max-small) {
            flex-wrap: wrap;

            .swiper-slide-img {
              width: 100%;
              max-width: 50%;

              &:last-of-type {
                display: none;
              }
            }
          }

          &-img {
            padding: 15px;

            img {
              display: block;
              max-width: 100%;
              height: auto;
            }
          }
        }
        &-button {
          &-prev {
            left: 0;
            @include respond(max-small) {
              left: 10px;
            }
          }
          &-next {
            right: 0;
            @include respond(max-small) {
              right: 10px;
            }
          }
        }
      }
    }

    &--release {
      padding-left: 50px;
      padding-right: 50px;
      padding-bottom: 30px;
      @include adaptive(575) {
        padding-left: 30px;
        padding-right: 30px;
      }

      .swiper {
        &-slide {
          display: flex;
          @include adaptive(1023) {
            flex-wrap: wrap;
          }

          &-left {
            width: 100%;
            max-width: 763px;
            padding-left: 50px;
            @include adaptive(1365) {
              max-width: 613px;
              padding-left: 0;
            }
            @include adaptive(1199) {
              max-width: 513px;
            }
            @include adaptive(1023) {
              max-width: 100%;
            }
          }

          &-right {
            flex: 1;
            padding-left: 41px;
            @include adaptive(1365) {
              padding-left: 30px;
            }
            @include adaptive(1023) {
              padding-top: 50px;
              padding-left: 0;
            }
            @include adaptive(575) {
              margin-left: -15px;
              margin-right: -15px;
              width: calc(100% + 30px);
            }
          }

          &-img {
            position: relative;
            width: 100%;
            height: 400px;
            @include adaptive(767) {
              height: 300px;
            }
            @include adaptive(575) {
              height: 200px;
            }

            img {
              position: absolute;
              top: 0;
              left: 0;
              width: 100%;
              height: 100%;
              object-fit: cover;
              object-position: center;
            }
          }

          &-title {
            padding-bottom: 30px;
            color: rgb(10, 23, 35);
            font-weight: bold;
            line-height: 1;
            @include fluid(font-size, 320px, 1366px, 30px, 48px);
            @include adaptive(767) {
              text-align: center;
            }
          }

          &-btn {
            display: flex;
            align-items: center;
            width: 100%;
            min-height: 60px;
            padding: 5px;
            border-radius: 5px;
            background-color: #315b99;

            > div {
              &:nth-of-type(1) {
                display: flex;
                align-items: center;
                justify-content: center;
                width: 50px;
                @include adaptive(575) {
                  display: none;
                }
              }

              &:nth-of-type(2) {
                flex: 1;
                padding-right: 15px;
                @include adaptive(575) {
                  padding-right: 0;
                }
              }
            }

            i,
            span {
              color: #ffffff;
            }

            .icon-font {
              font-size: 19px;
              @include adaptive(575) {
                display: none;
              }
            }

            span {
              color: rgb(255, 255, 255);
              font-weight: bold;
              line-height: 1.2;
              @include fluid(font-size, 320px, 767px, 12px, 18px);
              @include adaptive(575) {
                display: flex;
                justify-content: center;
                align-items: center;
              }
            }
          }

          &-info {
            ul {
              display: flex;
              flex-direction: column;
              @include adaptive(1023) {
                flex-wrap: wrap;
                flex-direction: row;
              }

              li {
                display: flex;
                flex-direction: column;
                padding-bottom: 30px;
                @include adaptive(1023) {
                  width: 100%;
                  max-width: 50%;
                  padding-right: 15px;
                }
                @include adaptive(767) {
                  padding-left: 15px;
                }
                @include adaptive(575) {
                  padding-left: 10px;
                  padding-right: 10px;
                }

                > div {
                  &:nth-of-type(1) {
                    display: flex;
                    align-items: center;
                    @include adaptive(767) {
                      justify-content: center;
                    }

                    img {
                      margin-left: 27px;
                      @include adaptive(575) {
                        width: 32px;
                      }
                    }

                    span {
                      color: rgb(10, 23, 35);
                      font-weight: bold;
                      line-height: 1;
                      @include fluid(font-size, 320px, 1366px, 23px, 48px);
                    }
                  }
                  &:nth-of-type(2) {
                    padding-top: 10px;
                    @include adaptive(767) {
                      text-align: center;
                    }

                    p {
                      color: rgb(10, 23, 35);
                      font-weight: 600;
                      line-height: 1;
                      @include fluid(font-size, 320px, 1366px, 15px, 21px);
                    }
                  }
                }
              }
            }
          }
        }

        &-button {
          &-prev,
          &-next {
            @include adaptive(1023) {
              top: 0;
              transform: translateY(135px);
            }
            @include adaptive(767) {
              transform: translateY(118px);
            }
            @include adaptive(575) {
              transform: translateY(68px);
            }
          }
        }

        &-pagination {
          bottom: 0;
          width: 100%;
          max-width: 713px;
          margin-left: 100px;
          @include adaptive(1365) {
            max-width: 613px;
            margin-left: 50px;
          }
          @include adaptive(1199) {
            max-width: 513px;
          }
          @include adaptive(1023) {
            bottom: auto;
            top: 410px;
            max-width: 100%;
            margin-left: 0;
          }
          @include adaptive(767) {
            top: 310px;
          }
          @include adaptive(575) {
            top: 210px;
          }

          &-bullet {
            opacity: 1;
            width: 7px;
            height: 7px;
            background-color: #95a6b2;

            &-active {
              background-color: #0a1723;
            }
          }
        }
      }
    }
  }

  &-button {
    &-prev,
    &-next {
      width: 31px;
      height: 130px;
      margin-top: 0;
      transform: translateY(-50%);
      background-repeat: no-repeat;
      background-size: contain;
      background-position: center;
      @include respond(max-small) {
        width: 15px;
        height: 64px;
      }
    }

    &-prev {
      background-image: url('../img/img-btn-prev-d.png');
      @include respond(max-small) {
        background-image: url('../img/img-btn-prev.png');
      }
    }

    &-next {
      background-image: url('../img/img-btn-next-d.png');
      @include respond(max-small) {
        background-image: url('../img/img-btn-next.png');
      }
    }
  }
}