// SECTION description :: START

.b-description {
  .c-grid {
    max-width: 1342px;
    @include respond(max-extra-small) {
      padding-left: 20px;
      padding-right: 20px;
    }
  }

  &__wrapper {
    display: flex;
    align-items: center;
    padding-top: 80px;
    padding-bottom: 80px;
    @include adaptive(767) {
     flex-wrap: wrap;
      padding-bottom: 0;
    }

    &--more {
      padding-top: 40px;
      padding-bottom: 40px;
    }

    &--reverse {
      flex-direction: row-reverse;
      @include adaptive(767) {
        flex-direction: row;
      }

      img {
        margin-left: auto;
        margin-right: auto;
      }
    }

    &-left,
    &-right {
      width: 50%;
      @include adaptive(767) {
        width: 100%;
      }
    }

    &-left {
      display: flex;
      align-items: center;
      justify-content: center;
      padding-right: 15px;
      @include adaptive(767) {
        padding-right: 0;
        padding-bottom: 15px;
      }

      img {
        display: block;
        max-width: 100%;
        height: auto;
        margin-right: auto;
      }
    }

    &-right {
      > div {
        width: 100%;
        max-width: 630px;
        @include adaptive(767) {
          text-align: center;
        }
      }

      img {
        display: block;
        max-width: 100%;
        height: auto;
        @include adaptive(767) {
          margin-left: auto;
          margin-right: auto;
        }
      }
    }
  }

  &__title {
    padding-bottom: 42px;
    color: rgb(10, 23, 35);
    font-size: 48px;
    font-weight: bold;
    @include adaptive(1279) {
      font-size: 36px;
    }
    @include adaptive(1023) {
      padding-bottom: 30px;
    }
    @include adaptive(767) {
      font-size: 24px;
      text-align: center;
    }
  }

  &__desc {
    display: block;
    padding-bottom: 30px;
    color: rgb(10, 23, 35);
    font-size: 18px;
    line-height: 1.6;
    @include adaptive(1279) {
      font-size: 16px;
    }
    @include adaptive(767) {
      font-size: 14px;
      text-align: center;
    }
  }

  &__btn {
    display: flex;
    align-items: center;
    width: 100%;
    max-width: 486px;
    margin-top: 42px;
    border-radius: 5px;
    background-color: #b1bec7;
    @include fluid(height, 320px, 1440px, 60px, 76px);
    @include transition(background-color);
    @include respond(max-small) {
      margin-left: auto;
      margin-right: auto;
    }
    @include adaptive(767) {
      margin-top: 0;
    }

    &:hover {
      background-color: #d72d42;
      @include transition(background-color);
    }

    &:active {
      background-color: #0a1723;
      @include transition(background-color);
    }

    > div {
      &:first-of-type {
        display: flex;
        justify-content: center;
        width: 110px;
        @include respond(max-small) {
          width: 80px;
        }

        span {
          display: flex;
          align-items: center;
          justify-content: center;
          width: 40px;
          height: 40px;
          border-radius: 100%;
          border: 2px solid $c-white;
          background-color: transparent;

          .icon-play {
            position: relative;
            left: 2px;
            color: $c-white;
            font-size: 15px;
          }
        }
      }

      &:last-of-type {
        width: 100%;
        max-width: calc(100% - 110px);
        @include respond(max-small) {
          max-width: calc(100% - 80px);
        }

        span {
          color: $c-white;
          line-height: line-height-convert(30, 18);
          font-size: 18px;
          font-weight: bold;
          @include fluid(font-size, 320px, 1440px, 16px, 18px);
        }
      }
    }
  }
}

// SECTION description :: END