// CONTACTS :: START

.contact {
  .c-grid {
    max-width: 1342px;
    @include respond(max-extra-small) {
      padding-left: 20px;
      padding-right: 20px;
    }
  }

  &__wrapper {
    z-index: 1;
    position: relative;
    @include fluid(padding-top, 320px, 1920px, 31px, 96px);
  }

  &__row {
    display: flex;

    &-1 {
      @include adaptive(1023) {
        flex-wrap: wrap;
      }

      .contact__row-left {
        position: relative;
        width: 500px;
        @include adaptive(1023) {
          order: 2;
          width: 100%;
        }

        &:before {
          content: '';
          position: absolute;
          top: 0;
          right: -50px;
          width: 2px;
          height: calc(100% - (56px + 16px));
          background-color: #8d96ae;
          @include adaptive(1199) {
            right: -30px;
          }
          @include adaptive(1023) {
            display: none;
          }
        }
      }
      .contact__row-right {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        position: relative;
        flex: 1;
        padding-left: 100px;
        min-height: 100%;
        padding-bottom: 72px;
        @include adaptive(1199) {
          padding-left: 60px;
        }
        @include adaptive(1023) {
          padding-left: 0;
          padding-bottom: 50px;
        }
        @include adaptive(575) {
          padding-bottom: 7px;
        }
      }
    }

    &-3 {
      padding-top: 60px;
      padding-bottom: 110px;
      @include respond(max-small) {
        padding-top: 30px;
        padding-bottom: 55px;
      }
    }
  }

  &__title,
  &__subtitle {
    line-height: 1.4;
    font-weight: bold;
  }

  &__title {
    @include fluid(font-size, 320px, 1920px, 24px, 71px);
    @include fluid(padding-bottom, 320px, 1920px, 30px, 63px);
  }

  &__subtitle {
    @include fluid(font-size, 320px, 1920px, 14px, 48px);
    @include fluid(padding-top, 320px, 1920px, 31px, 100px);
    @include fluid(padding-bottom, 320px, 1920px, 34px, 94px);
  }

  &__col {
    display: flex;
    align-items: center;
    @include respond(max-small) {
      flex-wrap: wrap;
    }
    @include adaptive(1023) {
      padding-bottom: 30px;
    }
    @include adaptive(575) {
      padding-bottom: 23px;
    }

    &-0 {
      .footer & {
        padding-bottom: 0;
        padding-right: 60px;
        @include respond(max-medium) {
          padding-right: 30px;
        }
        @include respond(max-small) {
          padding-right: 0;
          padding-bottom: 33px;
        }
      }
    }

    &-img {
      width: 139px;
      @include respond(max-small) {
        width: 36px;
        padding-bottom: 12px;
      }
      @include respond(max-extra-small) {
        width: 36px;
      }

      .footer & {
        width: 90px;
      }

      img {
        display: block;
        max-width: 100%;
        height: auto;

        .footer & {
          width: 64px;
          height: 64px;
        }
      }
    }

    &-text {
      display: flex;
      flex-direction: column;
      @include respond(max-small) {
        width: 100%;
      }

      p,
      span {
        line-height: 1;
      }

      p {
        color: #0a1723;
        line-height: 1;
        font-weight: bold;
        @include fluid(font-size, 320px, 1920px, 18px, 30px);
        @include fluid(padding-bottom, 320px, 1920px, 12px, 20px);

        .footer & {
          color: #434c62;
          @include fluid(font-size, 320px, 1920px, 18px, 24px);
          @include fluid(padding-bottom, 320px, 1920px, 10px, 16px);
        }
      }

      span {
        color: #d6273c;
        font-weight: 500;
        @include fluid(font-size, 320px, 1920px, 14px, 24px);

        .footer & {
          @include fluid(font-size, 320px, 1920px, 14px, 18px);
        }
      }
    }
  }

  &__map {
    position: relative;
    width: 100%;
    min-height: 379px;
    border-radius: 5px;
    background-color: #e4eaef;

    &:before {
      z-index: 0;
      content: '';
      position: absolute;
      bottom: -8px;
      left: 0;
      width: 100%;
      height: 100%;
      border-radius: 5px;
      border: 8px solid #e4eaef;
      background-color: #e4eaef;
    }

    #map {
      position: absolute !important;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      border-radius: 5px;
      border: 3px solid rgb(141, 150, 174);
    }

    &-info {
      display: flex;
      align-items: center;
      justify-content: flex-end;
      padding-top: 26px;
      @include fluid(padding-top, 320px, 1920px, 16px, 26px);

      p {
        margin-right: 24px;
        color: #393d47;
        font-weight: 600;
        @include fluid(font-size, 320px, 1920px, 15px, 24px);
        @include respond(max-extra-small) {
          margin-right: 0;
        }
      }

      i {
        @include respond(max-extra-small) {
          transform: scale(0.5);
        }
      }
    }
  }

  .c-form {
    &__title {
      display: none;
      padding-bottom: 27px;
      text-align: center;
      font-size: 14px;
      color: rgb(10, 23, 35);
      font-weight: bold;
      @include adaptive(767) {
        display: block;
      }
    }
    &__btn {
      border-radius: 28px;
      line-height: normal;

      &:before {
        display: none;
      }
    }
  }
}

// CONTACTS :: END