// SECTION release :: START

.release {
  &__wrapper {
    position: relative;
    @include fluid(padding-top, 320px, 1440px, 25px, 50px);
    @include fluid(padding-bottom, 320px, 1440px, 40px, 75px);
  }

  &__bg {
    z-index: 0;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;

    span {
      z-index: 0;
      position: absolute;
      left: 0;
      width: 100%;

      &:nth-child(1) {
        top: 0;
        height: 50%;
        background-color: #f8fcff;
        transform: skewY(-2deg);
        @include respond(max-extra-small) {
          top: -10px;
        }
      }

      &:nth-child(2) {
        bottom: 0;
        height: 50%;
        background-color: #f8fcff;
        transform: skewY(-2deg);
        @include respond(max-extra-small) {
          bottom: -10px;
        }
      }

      &:nth-child(3) {
        top: 0;
        height: 100%;
        background-color: #ecf7ff;
        transform: skewY(5deg);
      }
    }
  }

  &__row {
    z-index: 2;
    position: relative;
  }

  &__title {
    padding-left: 100px;
    padding-bottom: 36px;
    color: rgb(10, 23, 35);
    font-weight: bold;
    line-height: 1.25;
    @include fluid(font-size, 320px, 1366px, 24px, 48px);
    @include adaptive(1365) {
      padding-left: 50px;
    }
  }
}

// SECTION release :: END