// SECTION interested :: START

.interested {
  padding-top: 40px;
  padding-bottom: 95px;
  @include adaptive(767) {
    padding-bottom: 65px;
  }

  .c-grid {
    width: 100%;
    max-width: 1060px;
    margin-left: auto;
    margin-right: auto;
    @include adaptive(767) {
      padding-left: 20px;
      padding-right: 20px;
    }
  }

  &__wrapper {
    background-color: #ecf7ff;
    @include adaptive(767) {
      padding-top: 30px;
      padding-bottom: 30px;
    }
  }

  &__row {
    display: flex;
    align-items: center;
    justify-content: space-between;
    min-height: 285px;
  }

  &__col {
    &-1 {
      width: 250px;
      padding-right: 60px;
      @include adaptive(1279) {
        width: 200px;
        padding-right: 30px;
      }
      @include adaptive(767) {
        display: none;
      }
    }

    &-2 {
      position: relative;
      flex: 1;
      min-width: 1px;
      max-width: 100%;
    }

    img {
      display: block;
      max-width: 100%;
      height: auto;
    }
  }

  &__title {
    padding-bottom: 10px;
    font-size: 36px;
    font-weight: bold;
    @include adaptive(1279) {
      font-size: 30px;
    }
    @include adaptive(374) {
      font-size: 26px;
    }
  }

  &__desc {
    padding-bottom: 15px;
    font-size: 22px;
    @include adaptive(1279) {
      font-size: 16px;
    }
    @include adaptive(374) {
      font-size: 14px;
    }
  }

  &__input {
    &-1 {
      width: 340px;
      padding-right: 18px;
      @include adaptive(1279) {
        width: 220px;
      }
      @include adaptive(767) {
        width: 100%;
        max-width: 100%;
        padding-right: 0;
      }
    }

    &-2 {
      width: 100%;
      max-width: calc(100% - 340px);
      @include adaptive(1279) {
        max-width: calc(100% - 220px);
      }
      @include adaptive(767) {
        width: 100%;
        max-width: 100%;
      }
    }

    &-field {
      display: flex;
      @include adaptive(767) {
        flex-wrap: wrap;
      }
    }

    .c-form__input-wrapper {
      padding-bottom: 12px;
    }
  }

  &__btn {
    position: absolute;
    bottom: -80px;
    right: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 191px;
    height: 56px;
    border-radius: 2em;
    background-color: rgb(214, 39, 60);
    color: #ffffff;
    font-weight: bold;
    font-size: 24px;
    @include adaptive(1023) {
      bottom: -60px;
    }
    @include adaptive(767) {
      left: 0;
      height: 50px;
      margin-left: auto;
      margin-right: auto;
      font-size: 20px;
    }
  }
}

// SECTION interested :: END