// SECTION brand main :: START

.b-main {
  .section__bg--light {
    @include adaptive(767) {
      height: calc(100% + 100px);
      background-image: url('../img/img-bg--additional.png');
      //background-position: bottom right;
    }
  }

  .c-grid {
    max-width: 1342px;
    @include respond(max-extra-small) {
      padding-left: 20px;
      padding-right: 20px;
    }
  }

  &__wrapper {
    z-index: 2;
    position: relative;
    @include fluid(padding-top, 320px, 1920px, 41px, 96px);
  }

  &__head {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-bottom: 42px;
    @include adaptive(767) {
      flex-direction: column-reverse;
      align-items: center;
      padding-bottom: 20px;
    }

    > div {
      &:nth-of-type(1) {
        display: flex;
        align-items: center;
        @include adaptive(767) {
          width: 100%;
          padding-top: 20px;
          justify-content: space-between;

          .s-product & {
            justify-content: center;
          }
        }

        img {
          @include adaptive(767) {
            display: block;
            max-width: 125px;
            height: auto;
          }
        }
      }

      &:nth-of-type(2) {
        img {
          display: block;
          max-width: 100%;
          height: auto;
          @include adaptive(767) {
            margin-left: auto;
            margin-right: auto;
          }
        }
      }
    }

    p {
      padding-right: 45px;
      font-size: 36px;
      color: rgb(10, 23, 35);
      line-height: 1.653;
      @include adaptive(1279) {
        font-size: 30px;
      }
      @include adaptive(767) {
        font-size: 24px;
        padding-right: 15px;
      }

      .s-product & {
        padding-right: 0;
        padding-left: 45px;
        @include adaptive(767) {
          padding-left: 21px;
        }
      }
    }
  }

  &__body {
    &-line {
      width: 100%;
      max-width: 389px;
      height: 1px;
      background-color: rgb(149, 166, 178);
      margin-bottom: 42px;
      @include adaptive(767) {
        margin-bottom: 24px;
      }
    }

    p {
      font-size: 16px;
      color: rgb(79, 106, 122);
      line-height: 1.859;
      @include adaptive(767) {
        font-size: 14px;
        text-align: center;
      }
    }
  }
}

// SECTION brand main :: END